@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&family=Karla:wght@400;700&display=swap);
/* Header*/
.header {
    display: flex;
    place-items: center;
    height: 5rem;
    padding: 0 1.5rem;
    background-color: white;
}

.header svg {
    margin-right: auto;
}

.header button {
    height: 2rem;
    width: 2rem;
    font-size: 1.1rem;
    border: 1px solid hsl(270, 9%, 17%);
    color: hsl(270, 9%, 17%);
}

/*Navigation*/
.nav {
    position: absolute;
    top: 5rem;
    width: 100%;
    height: calc(100vh - 5rem);
    background-color: hsl(270, 9%, 17%);
    text-align: center;
    padding-top: 1rem;
    transition: 0.5s;
    
}

.nav:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 13.5rem;
    background: url(/FEM-Insure/static/media/mobile-nav.2922ed2c.svg);
    background-repeat: no-repeat;
    background-size: 100% 13.5rem;
}

nav li {
    margin-top: 1rem;
    display: flex;
    place-items: center;
}

nav li a {
    letter-spacing: 1px;
    width: 100%;
    padding: 1rem;
    color: white;
}

nav li a:active {
    border: 2px solid white;
}

.showNav {
    transform: translateX(0);
}

.hideNav {
    transform: translateX(calc(-100% - 80px));
}

@media screen and (min-width: 500px) {
    .header button {
        display: none;
    }

    .hideNav {
        transform: translateX(0);
    }
    /*Navigation*/
    .nav {
        position: relative;
        top: 0;
        height: 5rem;
        background-color: white;
        padding-top: 0;
    }
    
    .nav:after {
        display: none;
    }

    nav ul {
        display: flex;
        justify-content: flex-end;
    }

    nav li {
        margin-top: 1rem;
        display: flex;
        place-items: center;
    }
    
    nav li a {
        letter-spacing: 1px;
        width: 100%;
        padding: 1rem;
        color: black;
    }
}
.button {
    border: 1.5px solid white;
    background-color: transparent;
    padding: 0.7rem 2rem;
    color: white;
    letter-spacing: 1px;
    font-size: 0.8rem;
}
.hero {
    background-color: hsl(256, 26%, 20%);
    text-align: center;
    color: white;
    letter-spacing: 1.5px;
}

.hero hr {
    display: none;
}

.hero img {
    width: 100%;
}

.heroImageDesktop {
    display: none;
}

.heroIntro {
    padding: 6rem 1.5rem;
    position: relative;
}

.heroIntro:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 10rem;
    width: 100%;
    background: url(/FEM-Insure/static/media/bg-pattern-intro-left-mobile.562149a8.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.heroIntro:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -22rem;
    height: 100%;
    width: 7.5rem;
    background: url(/FEM-Insure/static/media/bg-pattern-intro-right-mobile.ef3d23bd.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.heroIntro p {
    margin: 2rem 0;
}

@media screen and (min-width: 800px) {
    .hero {
        display: flex;
        justify-content: center;
        position: relative;
        text-align: left;
    }

    .hero hr {
        display: block;
        width: 20%;
        color: hsl(0, 0%, 98%);
    }

    .heroIntro {
        width: 45%;
        padding: 12rem 1rem ;
    }
    
    .heroIntro:before, .heroIntro:after {
        display: none;
    }
    
    .heroIntro h1 {
        font-size: 5rem;
        padding-top: 5rem;
    }

    .hero:before {
        content: "";
        position: absolute;
        left: 0;
        bottom: -12rem;
        height: 60%;
        width: 20%;
        background: url(/FEM-Insure/static/media/bg-pattern-intro-left-desktop.600d6444.svg);
        background-repeat: no-repeat;
        background-size: contain;
    }
    
    .hero:after {
        content: "";
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 28%;
        background: url(/FEM-Insure/static/media/bg-pattern-intro-right-desktop.fc61cf1f.svg);
        background-repeat: no-repeat;
        background-size: contain;
    }

    .hero img {
        width: auto;
        top: 12rem;
        position: relative;
    }

    .heroImageMobile {
        display: none;
    }

    .heroImageDesktop {
        display: block;
    }
}
.mainCard {
    padding: 2rem 0;
}

.mainCard h2 {
    padding: 2rem 0 1rem 0;
}
.whyUs {
    text-align: center;
    padding: 7rem 1.5rem 0 1.5rem;
}

.whyUs hr {
    width: 40%;
    margin: auto;
}

.whyUs h1 {
    margin-top: 2.5rem;
}

@media screen and (min-width: 500px) {
    .whyUs {
        padding: 17rem 8rem 0;
        text-align: left;
    }

    .whyUs hr {
        width: 10%;
        margin: 0;
    }

    .card {
        display: flex;
        justify-content: space-between;
    }

    .mainCard {
        width: 30%;
    }
}
.howWeWork {
    background-color: hsl(256, 26%, 20%);
    color: white;
    text-align: center;
    padding: 3.5rem 1rem;
    margin:5rem 1.5rem 7rem;
    position: relative;
}

.howWeWork:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 90%;
    width: 50%;
    background: url(/FEM-Insure/static/media/bg-pattern-how-we-work-mobile.a4de14b0.svg);
    background-repeat: no-repeat;
    background-size: cover;
}

.howWeWork h1 {
    margin-bottom: 2rem;
}

@media screen and (min-width: 500px) {
    .howWeWork {
        margin:5rem 8rem 7rem;
        padding-left: 5rem;
        padding-right: 5rem;
        display: flex;
        align-items: center;
        text-align: left;
    }

    .howWeWork h1 {
        margin-right: auto;
        margin-bottom: 0;
        width: 36%;
        
    }

    .howWeWork:before {
        height: 100%;
        width: 100%;
        background: url(/FEM-Insure/static/media/bg-pattern-how-we-work-desktop.4abb7464.svg);
        background-size: 40% 100%;
        background-repeat: no-repeat;
        background-position: top right;
    }
}
.footerCard {
    width: 80%;
    margin: auto;
    display: flex;
    flex-direction: column;
    place-items: center;
}

.footerCard p:nth-child(1) {
    padding: 2rem 0;
    color: hsl(273, 4%, 51%);
    font-weight: 100;
}

@media screen and (min-width: 500px) {
    footer .card {
        width: 80%;
        margin: auto;
    }
    
    .footerCard {
        align-items: flex-start;
    }

    .footerCard  p {
        text-align: left;
    }
}
footer header {
    position: relative;
    padding: 5rem 0 0;
    z-index: 0;
}

footer header:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background: url(/FEM-Insure/static/media/bg-pattern-footer-mobile.c33e3a52.svg);
    background-size: 100% 90%;
    background-repeat: no-repeat;
}

footer svg {
    margin: 0px 7px;
}

footer hr {
    margin: 1rem;
}

.socialMedia {
    padding: 2rem;
}

@media screen and (min-width: 500px) {
    footer header {
        padding: 2rem 8rem;
    }
    
    .socialMedia {
        padding: 0;
    }

    footer svg {
        margin-right: auto;
    }

    footer header::before {
        display: none;
    }

    footer {
        position: relative;
    }

    footer hr {
        width: 80%;
        margin: auto;
    }

    footer:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background: url(/FEM-Insure/static/media/bg-pattern-footer-desktop.1586c300.svg);
        background-size: 35% 55%;
        background-repeat: no-repeat;
    }
}
header {
    position: fixed;
    width: 100%;
    z-index: 1000;
}

footer {
    text-align: center;
    background-color: hsl(0, 0%, 98%);
    padding-bottom: 5rem;
}

@media screen and (min-width: 500px) {
    header {
        display: flex;
    }
}
/*Resets*/

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    text-decoration: none;
    outline: none;
    list-style: none;
    letter-spacing: 1px;
}

:root {
    font-size: 16px;
}

/*General Styles*/

#root {
    display: grid;
    grid-template-rows: auto 1fr;
}

h1, h2, h3 {
    font-family: "DM Serif Display", serif;
}

h1 {
    font-size: 2.5rem;
    font-weight: 400;
    line-height: 1;
}

p {
    font-size: 0.9rem;
    line-height: 1.7;
    text-align: justify;
}

body {
    font-family: "Karla", sans-serif;
}
